import { Component, OnInit } from '@angular/core'
import { VendorVendorService } from 'src/app/sdk/asset/services'
import { Vendor } from 'src/app/sdk/asset/models'
import { NgxSpinnerService } from 'ngx-spinner'
import {
    FormBuilderTypeSafe,
    FormGroupTypeSafe,
} from 'src/app/services/angular-reactive-forms-helper'
import { PesanService } from 'src/app/services/tool/pesan.service'
import { Router } from '@angular/router'
import { FormControl, Validators } from '@angular/forms'

@Component({
    selector: 'app-add',
    templateUrl: './add.component.html',
    styleUrls: ['./add.component.scss'],
})
export class AddComponent implements OnInit {
    constructor(
        private vendorVendorService: VendorVendorService,
        private spinner: NgxSpinnerService,
        private fb: FormBuilderTypeSafe,
        private pesan: PesanService,
        private router: Router,
    ) {}

    data: Vendor

    ngOnInit() {
        this.form()
    }

    dataForm: FormGroupTypeSafe<Vendor>
    form() {
        this.dataForm = this.fb.group<Vendor>({
            name_vendor: new FormControl('', [Validators.required]),
            code: new FormControl(''),
            url: new FormControl(''),
            support_url: new FormControl(''),
            pic: new FormControl(''),
            phone: new FormControl(''),
            email: new FormControl(''),
            logo: new FormControl(''),
            country: new FormControl(''),
            address: new FormControl(''),
        })
    }
    simpan() {
        this.spinner.show()
        if (this.dataForm.invalid) {
            this.pesan.pesanWarningForm('Form Tidak Valid')
            this.spinner.hide()
            return
        }

        this.data = this.dataForm.value
        this.vendorVendorService.postVendorVendors(this.data).subscribe(
            data => {
                this.goToBack()
                this.spinner.hide()
            },
            err => {
                this.pesan.pesanWarningForm('Gagal di simpan')
                this.spinner.hide()
            },
            () => {
                console.log('HTTP request completed.')
            },
        )
    }

    goToBack() {
        this.router.navigate(['/vendor/vendor/list'])
    }
}
