import { Component, OnInit } from '@angular/core'
import { VendorVendorService, VendorContactService } from 'src/app/sdk/asset/services'
import { NgxSpinnerService } from 'ngx-spinner'
import {
    FormBuilderTypeSafe,
    FormGroupTypeSafe,
} from 'src/app/services/angular-reactive-forms-helper'
import { PesanService } from 'src/app/services/tool/pesan.service'
import { Router, ActivatedRoute } from '@angular/router'
import { Vendor, Contact } from 'src/app/sdk/asset/models'
import { FormControl, Validators } from '@angular/forms'
import { SingleUploadService } from 'src/app/components/document/single-upload.service'

@Component({
    selector: 'app-edit',
    templateUrl: './edit.component.html',
    styleUrls: ['./edit.component.scss'],
})
export class EditComponent implements OnInit {
    constructor(
        private vendorVendorService: VendorVendorService,
        private vendorContactService: VendorContactService,
        private spinner: NgxSpinnerService,
        private fb: FormBuilderTypeSafe,
        private pesan: PesanService,
        private router: Router,
        private singleUploadService: SingleUploadService,
        private activatedRouted: ActivatedRoute,
    ) {}

    data: Vendor
    listContact: Contact [] = []
    id_vendor: string = ''
    toogleUpload= false

    ngOnInit() {
        this.id_vendor = this.activatedRouted.snapshot.paramMap.get('id_vendor')
        this.form()
        this.getData()
        this.getContact()

        this.singleUploadService.getCallback().subscribe(
            data => {

                if (data == 'vendor.finish') {
                    this.toogleUpload = false;
                }
            }
        )

        this.singleUploadService.getId().subscribe(
            data => {
                if (data) {
                    this.dataForm.patchValue({ logo: data })
                    this.simpan()
                }
            }
        )
    }

    getContact() {
        this.vendorContactService.getVendorContacts(JSON.stringify({ref_id_module: this.id_vendor})).subscribe(
            data=> {
                this.listContact = data.data
            }
        )
    }

    togleShowUpload() {

        this.toogleUpload = true;
    }

    getData() {
        this.spinner.show()
        this.vendorVendorService.getVendorVendorsId(this.id_vendor).subscribe(data => {
            this.dataForm.setValue(data.data)
            this.spinner.hide()

            this.singleUploadService.param.next({
                for_module: "vendor",
                for_table: "erp_vendor.vendor",
                for_module_id: data.data.id_vendor
            })
        })
    }

    dataForm: FormGroupTypeSafe<Vendor>
    form() {
        this.dataForm = this.fb.group<Vendor>({
            name_vendor: new FormControl('', [Validators.required]),
            code: new FormControl(''),
            url: new FormControl(''),
            support_url: new FormControl(''),
            pic: new FormControl(''),
            phone: new FormControl(''),
            email: new FormControl(''),
            logo: new FormControl(''),
            country: new FormControl(''),
            address: new FormControl(''),
            id_vendor: new FormControl(''),
            id_user: new FormControl(''),
            created_at: new FormControl(''),
            updated_at: new FormControl(''),
        })
    }
    simpan() {
        this.spinner.show()
        if (this.dataForm.invalid) {
            this.pesan.pesanWarningForm('Form Tidak Valid')
            this.spinner.hide()
            return
        }

        this.data = this.dataForm.value
        this.vendorVendorService
            .putVendorVendorsId({ id: this.id_vendor, body: this.data })
            .subscribe(
                data => {
                    this.goToBack()
                    this.spinner.hide()
                },
                err => {
                    this.pesan.pesanWarningForm('Gagal di simpan')
                    this.spinner.hide()
                },
                () => {
                    console.log('HTTP request completed.')
                },
            )
    }

    goToBack() {
        this.router.navigate(['/vendor/vendor/list'])
    }
}
